<template>
  <div>
    <form @submit.prevent>
      <label>Descripción</label>
      <div><input type="text" v-model="descripcion" /></div>
      <label>Precio</label>
      <div><input type="number" v-model.number="precio_normal" /></div>
      <button @click="guardar()">Guardar</button>
      <button @click="cancelar()">Cancelar</button>
    </form>

    
  </div>
</template>


<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      descripcion: "",
      precio: 0,
            precio_normal: 0,
            precio_promo:0,
    };
  },
  methods: {
      ...mapActions(["agregarProductoCarrito"]),


    guardar() { 

      //console.log("Intento GUARDAR... agregarProductoCarrito: precio:",this.precio);

        this.agregarProductoCarrito({
              descripcion: this.descripcion,
              precio_normal: this.precio_normal,
              precio_promo: this.precio_promo,
              precio: this.precio_promo, //ddd 
            })
            .then(() => {
              //console.log("then agregarProductoCarrito: precio:",this.precio);
              this.$router.push({
                name: "ProductosCarrito",
              });
            });
    },
    cancelar() {
      this.$router.push({
        name: "ProductosCarrito",
      });
    },
  },
};
</script>