import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Productos from '../views/Productos.vue'
import ModificarProducto from '../views/ModificarProducto.vue'
import AgregarProducto from '../views/AgregarProducto.vue'
import EliminarProducto from '../views/EliminarProducto.vue'

 import Prepago from '../views/Prepago.vue'

import Carrito from '../views/Carrito.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/productos',
    name: 'Productos',
    component: Productos
  },
  {
    path: "/modificar/:id",
    name: "Modificar",
    component: ModificarProducto
  },
  {
    path: "/eliminar/:id",
    name: "Eliminar",
    component: EliminarProducto
  },
  {
    path: "/agregar",
    name: "Agregar",
    component: AgregarProducto
  },
  {
    path: "/carrito",
    name: "Carrito",
    component: Carrito
  },
  {
    path: "/prepago",
    name: "Carrito",
    component: Prepago ,
    //component: { render: (h) => h("h4" ,["Prepago  "]) }, 
  },
  {
    path: "/pronto",
    name: "Pronto",
    component: { render: (h) => h("h4" ,["Sitio en construcción. Pronto tendremos más novedades para ti...  "]) },
  },
  {
   //  si la pagina solicitada no existe... mostrara el mensaje correspondiente al error 404 
    path: "*",
    component: { render: (h) => h("div", ["Error 404! Pagina No Encontrada!"]) },
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
