<template>
  <b-container>
    <h4 class="font-weight-bold">Equipos & Accesorios</h4>
    <!-- ------------------------------------------------------------------- -->
    <hr />
    <h4>Nuevo Catalogo de Productos</h4>

    <b-row class="mt-3">
      <div
        v-for="equipo of productosCatalogo"
        :key="equipo.id"
        class="col-md-3 planes"
      >
        <b-card
          img-src="https://cdn.pixabay.com/photo/2013/07/12/12/01/photograph-145093_960_720.png"
          img-alt="Image"
          img-top
          border-variant="light"
        >
          <b-card-text>
            <div class="align-middle text-center pt-2">
              <h5>{{ equipo.nombre }}</h5>

              <p>{{ equipo.caracteristicas }}</p>
              <p>{{ equipo.precio_normal }}</p>
              <p>{{ equipo.precio_promo }}</p>

              <b-button
                @click="agregarProductoAcarrito(equipo.id)"
                type="button"
                class="btn btn-warning"
                >Agregar al Carrito</b-button
              >
            </div>
          </b-card-text>
        </b-card>
      </div>
    </b-row>

    <!-- ------------------------------------------------------------------- -->
    <!-- Fin Tabla que muestra la lista de productos -->
    <!-- ------------------------------------------------------------------- -->

    <div class="row">
      <div class="col">
        <div class="text-white bg-info text-wrap p-2 text-right">
          <h4>
            Hay
            <b-badge pill variant="danger">
              {{ totalProductosCarrito }}
            </b-badge>
            productos en el carrito
          </h4>
        </div>
      </div>
    </div>
    <div class="row">
      <div class="col pt-3">
        <b-button
          @click="irCarrito()"
          type="button"
          class="text-white bg-success float-right"
          >ir al Carrito</b-button
        >
      </div>
    </div>

    <hr />

    <hr />
  </b-container>
</template>

<script>
import { mapState } from "vuex";
import { mapActions } from "vuex";
import { mapGetters } from "vuex";

export default {
  data() {
    return {
      id: 0,
      descripcion: "",
      precio: 0,
      nombre: "",
      precio_promo: 0,
      precio_normal: 0,
      caracteristicas: "",
    };
  },
  computed: {
    // Enlaces unidireccionales desde el STORE a este componente.
    ...mapState(["catalogo"]), //traigo el objeto de catalogo
    //  ...mapState(["productos"]), //traigo el objeto de productos
    ...mapGetters(["totalProductosCarrito"]),
  

    ...mapState(["equipos"]),
    ...mapState(["planes"]),

    ...mapState(["productosCatalogo"]), //traigo el objeto de productosx
  },
  methods: {
    ...mapActions(["agregarProducto"]),

    //envia la id del producto del catalogo para ser agregada al carrito
    agregarProductoAcarrito(id) {
      console.log("agregarProductoAcarrito id:", id);
      this.agregarProducto({
        id: id,
      });
    },

    //envia a la pagina del carrito de compras
    irCarrito() {
      this.$router.push({ name: "Carrito" });
    },
  },
  created() {
    // this.$store.dispatch("getDashboarPlanes");
    this.$store.dispatch("getDashboarEquipos");
    /* getDashboarPlanes */
  },
};
</script>