
<template>
  <b-container>
    <!-- componente que requiere la confirmacion por parte del usuario para eliminar un producto 
    del carrito de compras -->
    <div class="bt-0 pt-1">
      <form @submit.prevent v-if="id">
        <h4>Eliminacion del producto del Carrito de compras</h4>
        <label>Código </label>
        <p>{{ id }}</p>
        <label>Descripción</label>
        <p>{{ descripcion }}</p>
        <label>Precio Unitario</label>
        <p>{{ precio }}</p>
        <label>cantidad de Productos</label>
        <p>{{ cantidad }}</p>
        <h4>
          ¿Esta seguro que quiere Eliminar el producto? si no es asi, presione
          cancelar
        </h4>
        <!-- boton para eliminar el producto seleccionado -->
        <b-button
          @click="eliminarDefinitivo()"
          type="button"
          class="btn btn-danger"
          >Eliminar</b-button
        >

        <!-- boton para cancelar la eliminacion del producto seleccionado -->
        <b-button @click="cancelar()" type="button" class="btn btn-secondary"
          >Cancelar</b-button
        >
 
      </form>
    </div>
  </b-container>
</template>

<script>
import { mapActions } from "vuex";
export default {
  data() {
    return {
      id: 0,
      descripcion: "",
      precio: 0,
      cantidad: 0,
    };
  },
  methods: {
    //enlaces al index.js del store
    ...mapActions(["eliminarProducto", "buscarProducto"]),

    //este metodo envia el id del producto a eliminar al vuex, donce lo recibe un action que lo deriva a un mutation
    eliminarDefinitivo() {
      this.eliminarProducto({
        id: this.id,
      }).then(() => {
        this.$router.push({
          name: "Carrito",
        });
      });
    },
    //este metodo cancela la eliminacion del producto y solo envia al usuario a la pagina del carrito
    cancelar() {
      this.$router.push({
        name: "Carrito",
      });
    },
  },
  //se montan al inicio de este componente los datos necesario a mostrar por pantalla
  mounted() {
    this.id = Number(this.$route.params.id);
    this.buscarProducto(this.id).then((producto) => {
      if (producto) {
        this.descripcion = producto.descripcion;
        this.precio = producto.precio;
        this.cantidad = producto.cantidad;
      } else {
        this.descripcion = "";
        this.precio = 0;
        this.cantidad = 0;
      }
    });
  },
};
</script>