<template>
  <b-container>
    <div>
      <!-- class="bt-0 pt-1" -->


     <!-- 
      <h4>Carrito de Compras</h4>

      <hr />
      <h4>Nuevo Carrito de Compras</h4>
      <hr />
      <div v-if="productosEnCarrito.length">
        <ul>
          <li v-for="equipo of productosEnCarrito" :key="equipo.id">
            <p>Id:{{ equipo.id }}</p>
            <p>Nombre:{{ equipo.nombre }}</p>
            <p>caracteristicas:{{ equipo.caracteristicas }}</p>
            <p>precio_normal:{{ equipo.precio_normal }}</p>
            <p>equipo.precio_promo:{{ equipo.precio_promo }}</p>
            <p>equipo.cantidad :{{ equipo.cantidad }}</p>
            <p>equipo.foto:{{ equipo.imagen }}</p>
          </li>
        </ul>
        <hr />
      </div>

      <hr />
      <h4>VIEJO Carrito de Compras</h4>

      -->

            <h4>  Carrito de Compras 2</h4>
      <!-- este dic solo se muestra si el largo del arreglo productos es mayor a cero, osea si es que hay productos en carrito -->
      <div v-if="productosEnCarrito.length">
        <!-- ------------------------------------------------------------------- -->
        <!-- Tabla que muestra la lista de productos, con id, foto, precio, los botones agregar,disminuir y eliminar productos
        del carrito  -->
        <table class="table table-striped table-bordered">
          <thead>
            <tr>
              <th scope="col" class="text-center text-white bg-dark">Id</th>
              <th scope="col" class="text-center text-white bg-dark">Foto</th>
              <th scope="col" class="text-center text-white bg-dark">
                Producto
              </th>
              <th scope="col" class="text-center text-white bg-dark">Precio</th>
              <th scope="col" class="text-center text-white bg-dark"></th>
              <th scope="col" class="text-center text-white bg-dark">
                Cantidad
              </th>
              <th scope="col" class="text-center text-white bg-dark"></th>
              <th scope="col" class="text-center text-white bg-dark">
                Eliminar
              </th>
            </tr>
          </thead>
          <tbody>
            <!-- despliegue de productos en la tabla por medio de v-for -->
            <tr v-for="equipo of productosEnCarrito" :key="equipo.id">
              <th scope="col" class="align-middle text-center">
                {{ equipo.id }}
              </th>
              <th scope="col" class="align-middle text-center">
                <img :src="equipo.foto" height="100" />
              </th>
              <th scope="col" class="align-middle">
                {{ equipo.nombre }}
              </th>
              <th scope="col" class="align-middle text-center">
                <p>Normal:{{ equipo.precio_normal }}</p>
                <p>Promo:{{ equipo.precio_promo }}</p>
              </th>
              <th scope="col" class="align-middle text-center">
                <!-- este boton decrementa de uno en uno los equipos agregados
                al carrito, si solo es uno envia a verificar antes de eliminarlo -->
                <b-button
                  @click="decrementarDeCarrito(equipo.id, equipo.cantidad)"
                  type="button"
                  class="btn btn-warning align-middle"
                  >-</b-button
                >
              </th>
              <th scope="col" class="align-middle text-center">
                {{ equipo.cantidad }}
              </th>
              <th scope="col" class="align-middle text-center">
                <!-- este boton incrementa de uno en uno los equipos agregados
                al carrito -->
                <b-button
                  @click="incrementarDeCarrito(equipo.id)"
                  type="button"
                  class="btn btn-warning"
                >
                  +
                </b-button>
              </th>
              <th scope="col" class="align-middle text-center">
                <!-- este boton  envia a verificar antes de eliminar el equipo seleccionado -->
                <b-button
                  @click="eliminarDeCarrito(equipo.id)"
                  type="button"
                  class="btn btn-danger"
                  >X</b-button
                >
              </th>
            </tr>
          </tbody>
        </table>
        <!-- Fin Tabla que muestra la lista de productos -->

        <!-- ------------------------------------------------------------------- -->
        <!-- se muestra el resumen de los productos agregados aal carrito, su cantidad y valor total -->
        <div class="text-right"><h4>Resumen:</h4></div>

        <div class="row">
          <div class="col">
            <div class="text-white bg-info text-wrap p-2 text-right">
              <h5>
                Hay
                <b-badge pill variant="danger">
                  {{ totalProductosCarrito }}
                </b-badge>
                productos en el carrito
              </h5>
            </div>
          </div>
        </div>
        <div class="row">
          <div class="col pt-3 text-right">
                        <p>SubTotal:  ${{totalPrecioBruto}}</p>
            <p>Descuentos:  ${{ totalDescuentoCarrito }}</p>
            <h4>
              Total costo Carrito:
              <b-badge pill variant="danger">
                ${{ totalCostoCarrito }}
              </b-badge>
            </h4>
          </div>
        </div>

        <!-- este boton es solo demostrativo de un carrito de compras, envia a un mensaje de sitio en construccion -->
        <b-button
          @click="irPagar()"
          type="button"
          class="btn btn-danger float-right"
          >ir a Pagar</b-button
        >
      </div>

      <!-- si no hay productos agregados al carrito , entonces mostrara un mensaje diciendolo y ademas
un boton que lleva al listado de productos  -->
      <div v-else>
        <div class="text-white bg-info text-wrap p-2 text-center">
          <h4>Aun no hay productos en tu carrito</h4>

          <b-button @click="irProductos()" type="button" class="btn btn-warning"
            >ir a Productos</b-button
          >
        </div>
      </div>
    </div>
    <!-- div final -->
  </b-container>
</template>

<script>
/* se importan los map necesarios desde vuex */
import { mapState } from "vuex";
import { mapGetters } from "vuex";
import { mapActions } from "vuex";

export default {
  data() {
    return {};
  },
  computed: {
    /* se crea un alias del objeto productos que se encuentra en el store */
    ...mapState(["productosEnCarrito"]),
    //se trae el computo del total del costo del carrito
    ...mapGetters(["totalCostoCarrito"]),
    //se trae el computo del total de productos del carrito
    ...mapGetters(["totalProductosCarrito"]),

          ...mapGetters(["totalDescuentoCarrito"]),//
...mapGetters(["totalPrecioBruto"]),


 //   ...mapState(["productosx"]), //traigo el objeto de productosx
  },
  methods: {
    ...mapActions(["incrementarProducto", "decrementarProducto"]),

    //metodo para eliminar del carrito el producto seleccionado, esto redirecciona hacia el componente eliminar
    eliminarDeCarrito(id) {
      this.$router.push({
        name: "Eliminar",
        params: {
          id: id,
        },
      });
    },
    //decrementa el producto seleccionado si la cantidad es mayor a uno, (esto se hace en el index.js del store)
    //, llevando consigo la id de este producto
    //si se decrementa y la cantidad es uno se envia al componente eliminar para confirmar eliminacion del producto
    //del carrito
    decrementarDeCarrito(id, cantidad) {
      if (cantidad == 1) {
        this.$router.push({
          name: "Eliminar",
          params: {
            id: id,
          },
        });
      } else {
        this.decrementarProducto({ id });
      }
    },
    //incrementa el producto seleccionado  (esto se hace en el index.js del store), llevando consigo la id de este producto
    incrementarDeCarrito(id) {
      //  console.log("id en carrito :",id);
      this.incrementarProducto({ id });
    },
    //envia al componente productos
    irProductos() {
      this.$router.push({
        name: "Productos",
      });
    },
    //envia a pagar (solo demostrativo, se muestra el mensaje en construccion)
    irPagar() {
      this.$router.push({
        name: "Pronto",
      });
    },
  },
  created() {
    // this.$store.dispatch("getDashboarPlanes");
    this.$store.dispatch("getDashboarEquipos");
    /* getDashboarPlanes */
  },
};
</script>