import Vue from 'vue'
import Vuex from 'vuex'
import axios from "axios";

const api = axios.create({
    // baseURL: 'api/',
    //baseURL: 'api/',
    baseURL: 'api/v1/bdc/',
    timeout: 1000,
})

Vue.use(Vuex)

export default new Vuex.Store({
    state: {

        equipos: [],
        planes: [],
        opiniones: [],

        form: {
            /* ejemplo api -------------- */
            email: "",
            nombre: "",
            banco: null,
            rut: "",
            num_cuenta: "",
            cuenta_origen: 1,
            monto: null,
            mensaje: "",
            /* ejemplo api -------------- */

            items: [],
            totales: [],
            clientes: [],
            despacho: {},
            formapago: ""
        },
        respuestaOrder: "",
        // Ahora productos[] es GLOBAL
        /* objeto que almacenara los productos agregados al carrito*/
        productosEnCarrito: [],
        productosCatalogo : [],


    },
    /* getters en vue se comportaba como computed */
    getters: {
        ultimoId(state) {
            return state.productosEnCarrito.reduce((a, e) => a < e.id ? e.id : a, 0);
         },
         /* calcula el total del costo del carrito */
          totalCostoCarrito(state ) {   
            let precioBruto = state.productosEnCarrito.reduce(
              (acumulado,  producto) => acumulado +  (producto.precio_promo*producto.cantidad),
              0);

              //console.log("precioBruto=",precioBruto);

              //logica para calcular descuento
              let costoTotalconDcto=0;
              let descuento=0;
             

              if(precioBruto<100000 && precioBruto>0){
                descuento =precioBruto*5/100;
                costoTotalconDcto=precioBruto-descuento;
              }else if(precioBruto>=100000  ){
                descuento =precioBruto*7/100;
                costoTotalconDcto=precioBruto-descuento;
              }

              //console.log("costoTotalconDcto=",costoTotalconDcto);

           return costoTotalconDcto;
        
         },  
         totalDescuentoCarrito(state ) {  
          let precioBruto = state.productosEnCarrito.reduce(
            (acumulado,  producto) => acumulado +  (producto.precio_promo*producto.cantidad),
            0);

            //console.log("precioBruto=",precioBruto);

            //logica para calcular descuento
           // let costoTotalconDcto=0;
            let descuento=0;
           

            if(precioBruto<100000 && precioBruto>0){
              descuento =precioBruto*5/100;
             // costoTotalconDcto=precioBruto-descuento;
            }else if(precioBruto>=100000  ){
              descuento =precioBruto*7/100;
              //costoTotalconDcto=precioBruto-descuento;
            }

            //console.log("costoTotalconDcto=",costoTotalconDcto);

         return descuento; 
         },
         totalPrecioBruto(state ) { 
          let precioBruto = state.productosEnCarrito.reduce(
            (acumulado,  producto) => acumulado +  (producto.precio_promo*producto.cantidad),
            0);
            return precioBruto; 
         },
         /* calcula el total de productos ingresados al carrito */
         totalProductosCarrito(state ) {   
           return state.productosEnCarrito.reduce(
             (acumulado,  producto) => acumulado +  (producto.cantidad),
             0
           );
         }, 

    },
    mutations: {
 

        storeEquipos(state, data) {
          console.log("storeEquipos(state, data) ");
         // console.log("DATA productos",data);
            state.productosCatalogo = data
        },
        storePlanes(state, data) {
            state.planes = data
        },
        storeOpiniones(state, data) {
            state.opiniones = data
        },


        /* ejemplo api banco */
        storeFormulario(state, data) {
            state.form = data
        },
        storeRespuesta(state, data) {
            state.respuestaOrder = data
        },
        /* actualiza el producto en el carrito, esta mutacion es accionada desde un action */
        actualizarProductoCarrito(state, payload) {
          console.log("STORE actualizarProductoCarrito(state, payload)" );
         //   let indice = state.catalogo.findIndex((producto) => producto.id == payload.id); //carrito viejo
            let indice = state.productosCatalogo.findIndex((producto) => producto.id == payload.id); //carrito nuevo
              console.log("MUT indice carrito:",indice);
            if (indice > -1) {
/*                payload.descripcion =state.catalogo[indice].descripcion;
               payload.precio = state.catalogo[indice].precio;
               payload.cantidad = state.catalogo[indice].cantidad;      
               payload.foto = state.catalogo[indice].foto; */

                //carrito nuevo
               payload.nombre =state.productosCatalogo[indice].nombre;
               payload.descripcion =state.productosCatalogo[indice].caracteristicas;
               payload.precio_normal = state.productosCatalogo[indice].precio_normal;
               payload.precio_promo = state.productosCatalogo[indice].precio_promo;
               payload.cantidad = state.productosCatalogo[indice].precio_promo;
               payload.imagen = state.productosCatalogo[indice].imagen;



              /* 
              si id de catalogo coincide con alguna id de carrito 
              entonces hay que resolver en cual indice esta esa id
              y incrementar su cantidad
              */
             // console.log("payload.id: ",payload.id);
      
             //busco en el carrito si ya existe un elemento que tenga la misma id del producto seleccionado...
              let indiceCarrito = state.productosEnCarrito.findIndex((producto) => producto.id == payload.id);
      
              //console.table(state.productos);
      
              //si la id es encontrada entonces el producto no se vuelve a grabar en el carrito, pero en cambio 
              //aumento el contador que esta asociado a la cantidad de ese producto en el carrito
              if (indiceCarrito > -1) {   
                state.productosEnCarrito[indiceCarrito].cantidad = state.productosEnCarrito[indiceCarrito].cantidad +1 ;  
              }
              //si no existe la id del producto seleccionado en el objeto del carrito, entonces lo agrego al final de este
              else{
                 /* grabo el objeto del catalogo en el objeto del carrito */
                state.productosEnCarrito.push({
                id: payload.id,
/*                 descripcion: payload.descripcion,
                precio: payload.precio,
                cantidad: 1,
                foto: payload.foto, */
                nombre: payload.nombre,
                caracteristicas: payload.caracteristicas,
                precio_normal:payload.precio_normal,
                precio_promo:payload.precio_promo,
                cantidad: 1,
                imagen: payload.imagen,
              });
      
            }
       
            }
          },
          decrementarProducto(state, payload) {
            let indice = state.productosEnCarrito.findIndex((producto) => producto.id == payload.id);
            if (indice > -1) {
      
              //si el producto fue encontrado, se discrimina si su cantidad acumulada en el carrito es mayor a uno 
              //si es asi se decrementa y si no se elimina
                if(state.productosEnCarrito[indice].cantidad>1)
                {state.productosEnCarrito[indice].cantidad--;
                 // console.log("cantidad despues decre:",state.productos[indice].cantidad);
                }
                else{
                  state.productosEnCarrito.splice(indice, 1);
               //   console.log("producto Eliminado" );
                }
             
            }
          },
          eliminarProducto(state, payload) {
            let indice = state.productosEnCarrito.findIndex((producto) => producto.id == payload.id);
            if (indice > -1) {
      
              //si el producto fue encontrado, se elimina
              state.productosEnCarrito.splice(indice, 1);
             
            }
          },
          incrementarProducto(state, payload) {
            let indice = state.productosEnCarrito.findIndex((producto) => producto.id == payload.id);
      
            //console.log("payload.id:",payload.id);
            //console.log("indice:",indice);
      
            if (indice > -1) {
              //si el producto fue encontrado, se discrimina si su cantidad acumulada en el carrito es mayor a cero 
              //si es asi se incrementa  
                if(state.productosEnCarrito[indice].cantidad>0)
                {state.productosEnCarrito[indice].cantidad++;
                 // 
                }  
            }
          },
          agregarProducto(state, payload) {
           // console.log("STORE agregarProducto(state, payload)" );
            //equipo.precio_promo
            state.productosEnCarrito.push({
              id: payload.id,
              descripcion: payload.descripcion,
              precio: payload.precio
            });
          },
      
    },
    actions: {
 

        getDashboarPlanes({ commit }) {
            const request = api.get('home.json')
                // console.log("request :",request );
            request.then(response => {
                commit('storePlanes', response.data.planes)
            })
        },

        getDashboarOpiniones({ commit }) {
            const request = api.get('home.json')
                //   console.log("request :",request );
            request.then(response => {
                commit('storeOpiniones', response.data.opiniones)
            })
        },

        getDashboarEquipos({ commit }) {
            const request = api.get('equipos.json')
                // console.log("request :",request );
               // console.log(" getDashboarEquipos({ commit }) ");
            request.then(response => {
                commit('storeEquipos', response.data.productos)
                console.log("getDashboarEquipos OK...");
               // console.log("response.data.productos...",response.data.productos);
            })
        },

        postTransfer({ state }) {
            const formData = new FormData()
            formData.append('banco', state.form.banco)
            formData.append('cuenta_origen', state.form.cuenta_origen)
            formData.append('email', state.form.email)
            formData.append('mensaje', state.form.mensaje)
            formData.append('monto', +state.form.monto)
            formData.append('nombre', state.form.nombre)
            formData.append('num_cuenta', state.form.num_cuenta)
            formData.append('rut', state.form.rut)

            const request = api.post('env_transf_req.json', formData)
            request.then(response => {
                console.log('respuesta', response)
            })
        },
        buscarProducto(context, id) {
            return new Promise((resolve) => {
              let indice = context.state.productosEnCarrito.findIndex((producto) => producto.id == id);
              if (indice > -1) {
                resolve(context.state.productosEnCarrito[indice]);
              } else {
                resolve(undefined);
              }
            });
          },
          actualizarProducto(context, payload) {
            return new Promise((resolve) => {
              context.commit('actualizarProducto', payload);
              resolve();
            });
          },
          agregarProducto(context, id) {
            return new Promise((resolve) => { 
              context.commit('actualizarProductoCarrito',  
                id  
               );
              resolve();
            });
          },
          decrementarProducto(context, payload) {
      
           // console.log("Action payload:",payload);
      
            return new Promise((resolve) => {
              context.commit('decrementarProducto', payload);
              resolve();
            });
          },
          incrementarProducto(context, payload) {
      
            return new Promise((resolve) => {
              context.commit('incrementarProducto', payload);
              resolve();
            });
          },
          eliminarProducto(context, payload) {
      
            // console.log("Action payload:",payload);
       
             return new Promise((resolve) => {
               context.commit('eliminarProducto', payload);
               resolve();
             });
           },
    },
    modules: {},


})